.intro-header {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  padding-top: 10rem;
}

.intro-header__title,
.intro-header__subtitle {
  position: relative;
  z-index: 2;
}

/* TITLE (NAME)
============================*/
.intro-header__title {
  font-size: 1.5rem;
  text-align: center;
  color: var(--header-text-before);
  font-weight: 700;
  transform: scaleY(.85);
  cursor: help;
}

.intro-header__title:hover {
  background: rgba(255, 255, 255, 0.3);
}

.intro-header__title:hover:after {
  content: "(auto-fee)";
  position: absolute;
  bottom: 1.375rem;
  right: 0;
  transform: translateX(115%);
  font-size: 1rem;
  font-weight: 500;
  color: var(--header-text-before);
}

.intro-header__title:hover:before {
  content: "ô′tō fē";
  position: absolute;
  bottom: 0;
  right: 0;
  transform: translateX(140%);
  font-size: 1rem;
  font-weight: 500;
  color: var(--header-text-before);
}

/* SUBTITLES
============================*/
.intro-header__subtitles {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  height: 100%;
  margin-top: 2rem;
}

.intro-header__subtitle {
  display: flex;
  align-items: center;
  width: 100%;
  justify-content: center;
}

.intro-header__subtitle--two {
  padding-top: .125rem;
}

.intro-header__subtitle--text {
  line-height: 1;
  letter-spacing: -.1rem;
  font-size: 3rem;
  color: var(--header-text-before);
}

.intro-header__subtitle img {
  height: 1.5rem;
  width: 1.5rem;
  margin: 0 .875rem;
}